.Home{
    width: 100%;
    height: 100%;
    position: relative;
}
.Home::before{
    content:"";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: linear-gradient(311.76deg, #D4E7FE -15.24%, #FFFFFF 78.85%);
    opacity: 1;
    z-index:-1;
}
